html, body, #root, #root > div {
    height: 100%;
}

body {
    background-color: #27272c;
}

h1 {
    font-family: 'Poppins', sans-serif;
    color: #00cdff;
    border-bottom: 5px solid #fff41f;
}

h2 {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    color: #d70190;
}


h3 {
    font-family: "Source Code Pro", sans-serif;
    margin: 10px 0;
    color: #6366ff;
}

h4 {
    font-family: 'Poppins', sans-serif;
    font-weight: lighter;
    color: #fff41f;
    font-size: 15px;
    margin: 0 0 10px;
}

h5 {
    font-family: 'Poppins', sans-serif;
    font-weight: lighter;
    color: #ff9999;
    font-size: 15px;
    margin: 0 0 8px;
}

p {
    font-family: "Source Code Pro", sans-serif;
    color: #212121;
    margin: 0 0 5px;
}

a {
    color: #d70190;
    text-decoration: none;
}

a:hover {
    color: #6366ff;
}


.app-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 100px;
}

.app-div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 750px;
}

.app-title {
    margin: 0 0 10px;
}

.counter-div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin: 0 10px 40px;
}

.correct-counter {
    border: 1px solid aquamarine;
    padding: 10px;
    border-radius: 30px;
    background-color: aquamarine;
    margin-right: 5px;
    box-shadow: 0 3px 0 #00bc7a, 0 3px 3px rgba(0,0,0,.4), inset 0 1px 0 rgba(255,255,255,.3), inset 0 0 3px rgba(255, 255, 255, 0.5);
}

.incorrect-counter {
    border: 1px solid #ff9999;
    padding: 10px;
    border-radius: 30px;
    background-color: #ff9999;
    margin-left: 5px;
    box-shadow: 0 3px 0 #9d5a5a, 0 3px 3px rgba(0,0,0,.4), inset 0 1px 0 rgba(255,255,255,.3), inset 0 0 3px rgb(255, 255, 255);
}

.quiz-text {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 20px 0;
}

.quiz-results {
    border: 3px dashed aquamarine;
    width: 250px;
    height: auto;
    padding: 20px;
}

.quiz-results p {
    color: white;
}

.results-row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.keyboard {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-family: 'Poppins', sans-serif;
    box-shadow: 1px 5px 10px #000000;
    margin: 0 0 10px;
}

.white-key {
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    color: black;
    background-color: white;
    border: 1px solid lightgray;
    height: 220px;
    width: 60px;
    margin: 0 -5px 0 0;
}

.black-key {
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    background-color: black;
    z-index: 10;
    margin: 0 0 0 -10px;
    width: 45px;
    height: 150px;
    border: 0;
    border-top: 1px solid lightgray;
}

.pressed-key {
    background-color: plum;
}

.black-key + .white-key {
    margin-left: -32px;
}

.white-key, .black-key {
    font-size: 10px;
    font-family: 'Poppins', sans-serif;
}

.white-key:hover, .black-key:hover {
    background-color: plum;
}
